import { Grid, Paper, useMediaQuery, useTheme } from "@mui/material";
import AppPagination from "../../app/components/AppPagination";
import CheckboxButtons from "../../app/components/CheckboxButtons";
import RadioButtonGroup from "../../app/components/RadioButtonGroup";
import useProducts from "../../app/hooks/useProducts";
import LoadingComponent from "../../app/layout/LoadingComponent";
import { useAppDispatch, useAppSelector } from "../../app/store/configureStore";
import { setPageNumber, setProductParams } from "./catalogSlice";
import ProductList from "./ProductList";
import ProductSearch from "./ProductSearch";
import ProductsFiltersAccordion from "./ProductsFiltersAccordion";

const sortOptions = [
  { value: "name", label: "Alphabetical" },
  { value: "priceDesc", label: "Price - High to Low" },
  { value: "price", label: "Price - Low to High" },
];

export default function Catalog() {
  const { products, brands, types, filtersLoaded, metaData } = useProducts();
  const { productParams } = useAppSelector((state) => state.catalog);
  const dispatch = useAppDispatch();
  const theme = useTheme();
  const isMatching = useMediaQuery(theme.breakpoints.down("md"));

  if (!filtersLoaded) return <LoadingComponent message="Loading products..." />;

  return (
    <Grid container columnSpacing={4}>
      <Grid item xs={12} md={3} mb={{ xs: 3, md: 0}}>
        <Paper sx={{ mb: 2 }}>
          <ProductSearch />
        </Paper>

        {isMatching ? (
          <>
            <ProductsFiltersAccordion
              name="Order By"
              children={
                <Paper sx={{ mb: 2, p: 2 }}>
                  <RadioButtonGroup
                    selectedValue={productParams.orderBy}
                    options={sortOptions}
                    onChange={(e) =>
                      dispatch(setProductParams({ orderBy: e.target.value }))
                    }
                  />
                </Paper>
              }
            />
            <ProductsFiltersAccordion
              name="Filter by Brands"
              children={
                <Paper sx={{ mb: 2, p: 2 }}>
                  <CheckboxButtons
                    items={brands}
                    checked={productParams.brands}
                    onChange={(items: string[]) =>
                      dispatch(setProductParams({ brands: items }))
                    }
                  />
                </Paper>
              }
            />

            <ProductsFiltersAccordion
              name="Filter by Types"
              children={
                <Paper sx={{ mb: 2, p: 2 }}>
                  <CheckboxButtons
                    items={types}
                    checked={productParams.types}
                    onChange={(items: string[]) =>
                      dispatch(setProductParams({ types: items }))
                    }
                  />
                </Paper>
              }
            />
          </>
        ) : (
          <>
            <Paper sx={{ mb: 2, p: 2 }}>
              <RadioButtonGroup
                selectedValue={productParams.orderBy}
                options={sortOptions}
                onChange={(e) =>
                  dispatch(setProductParams({ orderBy: e.target.value }))
                }
              />
            </Paper>
            <Paper sx={{ mb: 2, p: 2 }}>
              <CheckboxButtons
                items={brands}
                checked={productParams.brands}
                onChange={(items: string[]) =>
                  dispatch(setProductParams({ brands: items }))
                }
              />
            </Paper>
            <Paper sx={{ mb: 2, p: 2 }}>
              <CheckboxButtons
                items={types}
                checked={productParams.types}
                onChange={(items: string[]) =>
                  dispatch(setProductParams({ types: items }))
                }
              />
            </Paper>
          </>
        )}
      </Grid>
      <Grid item xs={12} md={9}>
        <ProductList products={products} />
      </Grid>
      <Grid item xs={3}></Grid>
      <Grid item xs={9} sx={{ mb: 2, mt: 2 }}>
        {metaData && (
          <AppPagination
            metaData={metaData}
            onPageChange={(page: number) =>
              dispatch(setPageNumber({ pageNumber: page }))
            }
          />
        )}
      </Grid>
    </Grid>
  );
}
